@font-face{
  font-family:"GmarketSansTTFLight";
  src:local('GmarketSansTTFLight'),
    url('./fonts/GmarketSansTTFLight.ttf') format('truetype');
}
@font-face{
  font-family:"GmarketSansTTFBold";
  src:local('GmarketSansTTFBold'),
    url('./fonts/GmarketSansTTFBold.ttf') format('truetype');
}
@font-face{
  font-family:"GmarketSansTTFMedium";
  src:local('GmarketSansTTFMedium'),
    url('./fonts/GmarketSansTTFMedium.ttf') format('truetype');
}

p{
  font-family:"GmarketSansTTFLight";
  font-size: medium;
}
p.priority {
  color:#fff;
}

b{
  font-family:"GmarketSansTTFBold";
  font-size: medium;
  color:#3F51B5;
}
b.idt{
  font-family:"GmarketSansTTFBold";
  font-size: medium;
  color:#fff;
}
h1{
  font-family:"GmarketSansTTFBold";
  color:#3F51B5;
}
/* h2 = on_card title */
h2{ 
  font-family:"GmarketSansTTFBold";
  color:#3F51B5;
  font-size: large;
}

/* h3 = off_card title */
h3{ 
  font-family:"GmarketSansTTFBold";
  color:#ddd;
  font-size: large;
}

.comp{
  display: 'flex';
  align-items: 'center';
  justify-content: 'center';
  width:width*0.8;
}